<template>
  <div class="login-container">
    <form @submit.prevent>
      <h1 class="title">  </h1>
      <input
        type="text"
        id="login"
        name="user_login"
        v-model="username"
        placeholder=" ..."
        :class="{ invalid: v$.username.$error }"
      />
      <small v-if="v$.username.$error" class="validate-error-message" id="test">
            !
      </small>

      <input
        type="password"
        id="password"
        name="user_password"
        v-model="password"
        placeholder=" ..."
        :class="{ invalid: v$.password.$error }"
      />
      <small
        v-if="v$.password.$dirty && v$.password.required.$invalid"
        class="validate-error-message"
      >
           !
      </small>
      <small
        v-if="v$.password.minLength.$invalid"
        class="validate-error-message"
      >
             3 !
      </small>

      <div class="save-container">
        <input
          type="checkbox"
          id="save"
          name="selected_save"
          v-model="isSavedSession"
        />
        <label for="save" class="save-label"></label>
      </div>
      <button type="submit" @click="touchLogIn"></button>

      <div v-if="isHaveErrorAuth()" class="error-container">
        {{ this.$store.state.auth.errorMessage }}
      </div>
    </form>
  </div>
</template>

<script>
import { mapActions } from "vuex";
import useVuelidate from "@vuelidate/core";
import { required, minLength } from "@vuelidate/validators";

export default {
  setup() {
    return {
      v$: useVuelidate(),
    };
  },

  data() {
    return {
      username: "",
      password: "",
      isSavedSession: false,
      passwordPlaceholder: " ...",
      loginPlaceholder: " ...",
    };
  },

  validations() {
    return {
      username: { required },
      password: { required, minLength: minLength(4) },
    };
  },

  methods: {
    ...mapActions(["logIn"]),

    async touchLogIn() {
      const isCorrectForm = await this.v$.$validate();
      if (!isCorrectForm) {
        return;
      }

      const isAuth = await this.logIn({
        username: this.username,
        password: this.password,
        isSavedSession: this.isSavedSession,
      });

      if (isAuth) {
        this.$router.push("/");
        return;
      }

      this.$router.push("/login");
    },

    isHaveErrorAuth() {
      return this.$store.state.auth.errorMessage !== "";
    },

    isValidForm() {
      const MAX_PASSWORD_LEN = 4;

      this.validationErrors = [];

      if (this.username.length && this.password.length >= MAX_PASSWORD_LEN) {
        return true;
      }

      return false;
    },
  },
};
</script>

